@mixin hoverAnimParams($transform, $animationName, $duration:1.5s, $delay:0.3s) {
  -webkit-transform: translateY($transform);
  transform: translateY($transform);
  -webkit-animation-name: $animationName;
  animation-name: $animationName;
  -webkit-animation-duration: $duration;
  animation-duration: $duration;
  -webkit-animation-delay: $delay;
  animation-delay: $delay;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

@-webkit-keyframes hover {
  50% {
    -webkit-transform: translateY(-3px);
    transform: translateY(-3px);
  }

  100% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
  }
}

@keyframes hover {
  50% {
    -webkit-transform: translateY(-3px);
    transform: translateY(-3px);
  }

  100% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
  }
}

@keyframes hoverActive {
  50% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
  }

  100% {
    -webkit-transform: translateY(-12px);
    transform: translateY(-12px);
  }
}

@-webkit-keyframes hoverShadow {
  0% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
    opacity: .4;
  }

  50% {
    -webkit-transform: translateY(3px);
    transform: translateY(3px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
    opacity: .4;
  }
}

@keyframes hoverShadow {
  0% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
    opacity: .4;
  }

  50% {
    -webkit-transform: translateY(3px);
    transform: translateY(3px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
    opacity: .4;
  }
}

@keyframes hoverShadowActive {
  0% {
    -webkit-transform: translateY(12px);
    transform: translateY(12px);
    opacity: .4;
  }

  50% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translateY(12px);
    transform: translateY(12px);
    opacity: .4;
  }
}

.hoverShadow {
  //display: inline-block;
  position: relative;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.hoverShadow:before {
  pointer-events: none;
  position: absolute;
  z-index: -1;
  content: '';
  top: 100%;
  left: 5%;
  height: 10px;
  width: 90%;
  opacity: 0;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0) 80%);
  /* W3C */
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform, opacity;
  transition-property: transform, opacity;
}

.hoverShadow:hover, .hoverShadow:focus {
  @include hoverAnimParams(-6px, hover);
}

.hoverShadow:hover:before, .hoverShadow:focus:before {
  opacity: .4;
  @include hoverAnimParams(6px, hoverShadow);
}

.hoverShadow:active {
  @include hoverAnimParams(-12px, hoverActive, 2.5s, 0s);
}

.hoverShadow:active:before {
  opacity: .4;
  @include hoverAnimParams(12px, hoverShadowActive, 2.5s, 0s);
}

.active {
  color: rgb(50, 148, 63);
  @include hoverAnimParams(-6px, hover);
}

.active:before{
  opacity: .4;
  @include hoverAnimParams(6px, hoverShadow);
}
